
body {
  color: #333;
  font-size: 14px;
}
h1.h1-title {
  font-size: 18px;
  font-weight: bold;
  line-height: 40px;
  border-bottom: 1px solid #E6E6E6;
}
.h1-content {
  margin: 10px 0 20px;
  font-size: 14px;
}
.h1-content a {
  color: #6772E5;
}
.vui-page-title {
  margin-top: 20px;
  margin-bottom: 5px;
  font-weight: bold;
}
.vui-p {
  margin-bottom: 10px;
}
.warper {
  width: 100%;
  min-width: 80%;
}
.warper-content {
  width: 80%;
  margin: 0 auto;
  margin-top: 20px;
}
.content {
  margin: 0 auto;
  position: relative;
}
.menu {
  float: left;
  background: #E6E6E6;
  width: 280px;
  margin-right: 20px;
}
.menu p {
    text-decoration: none;
    cursor: pointer;
}
.main {
  padding: 20px 0 20px 300px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  min-height: 300px;
}
.clear {
  zoom: 1;
}
.clear:before,
.clear:after {
  display: table;
  content: "";
}
.clear:after {
  clear: both;
}
.menu-list {
  /* height: 30px;
    line-height: 30px; */
  font-size: 14px;
  text-indent: 1em;
}
.menu-list p:hover > a {
    text-decoration: underline;
}
.menu-active {
  background: #9CA3F6;
  color: #fff;
}
.mr-b5 {
  margin-bottom: 5px;
}
.mr-b10 {
  margin-bottom: 10px;
}
pre {
  position: relative;
  word-wrap: break-word;
  white-space: pre-wrap;
  padding: 6px 10px;
  background-color: #f5f5f5;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
  color: #666;
  margin: 10px 0;
}



