$range-class: #{$prefix-cls}range;
#{$range-class} {
    position: relative;
    display: inline-block;
    width: 300px;
    height: 6px;
    background: $border-color-little;
    border-radius: 6px;
    cursor: pointer;
    label {
        position: absolute;
        display: inline-block;
        width: 100%;
        height: 100%;
        background: $primary-color;
        top: 0;
        border-radius: 6px;
        cursor: inherit;
        transition: all ease-in-out 0.2s;
    }
    span {
        position: absolute;
        top: -4px;
        display: inline-block;
        box-sizing: border-box;
        cursor: inherit;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        border: 2px solid $primary-color;
        background: #fff;
    }
    &:not(#{$range-class}-disabled) span:hover {
        @extend #{$prefix-cls}box-shadow;
        transform: scale(1.1);
    }
    &-start {
        left: -7px;
    }
    &-end {
        right: -7px;
    }
}

#{$range-class}-disabled {
    cursor: not-allowed;
    span {
        border: 2px solid $border-color-main;
    }
}



